export interface AcademicSelectedInfo {
  school_code: string;
  school_name: string;
  qualification_id: string;
  qualification_name: string;
  position_id: string;
  position_name: string;
}

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  FC_GetAssignAssets: FT_GetAssignAssets;
  success: (data: AcademicSelectedInfo) => void;
  district_id: string;
  school?: { school_id: string; school_name: string };
  user_id: string;
}

interface AppState {
  loading_assets: boolean;
  loading: boolean;
  error: {
    target:
      | "main-success"
      | "main-error"
      | null
      | "loading_school"
      | "waiting-list-error"
      | "get-teacher-on-waiting-success"
      | "input-number-error"
      | "send-list-to-tmis-error"
      | "send-list-to-tmis-success"
      | "get-teachers-from-trs-success"
      | "get-teacher-on-waiting-error";
    msg: string;
  };
  school_loading: boolean;
  school_details: API_DataGetSchoolRegister | null;
  district_code: string;
  selected_school: SchoolInterface | null;
  search_school: string;
  waiting_list: WaitingList[];
  loading_waiting_list: boolean;
  open_trs_import: boolean;
  selected_position: SchoolPositionItem | null;
  trs_found_position: WaitingList | null;
  selected_total_post_allowed_on_school: number;
  input_requesting_number: number;
  loading_candidates_from_trs: boolean;
  selected_post_request_id: string;
  collected_teachers: ReturnedCandidates[];
  collected_candidates_open: boolean;
  loading_send_list_to_tmis: boolean;
}

export interface SchoolPositionItem {
  position_code: string;
  position_name: string;
  qualification_id: string;
  qualification_name: string;
}
class _RegisterTeacherDDE extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      open_trs_import: false,
      loading_waiting_list: false,
      waiting_list: [],
      loading_assets: true,
      loading: false,
      error: {
        target: null,
        msg: "",
      },
      district_code: this.props.district_id ? this.props.district_id : "",
      selected_school: null,
      search_school: "",
      school_loading: true,
      school_details: null,
      selected_position: null,
      trs_found_position: null,
      selected_total_post_allowed_on_school: 0,
      input_requesting_number: 0,
      loading_candidates_from_trs: false,
      selected_post_request_id: "",
      collected_teachers: [],
      collected_candidates_open: false,
      loading_send_list_to_tmis: false,
    };
  }

  componentDidMount() {
    // Load data if they are not found
    if (
      this.props.basic.assign_teacher_assets.loading === true ||
      this.props.basic.assign_teacher_assets.data === null
    ) {
      this.props.FC_GetAssignAssets(this.props.district_id, (status, msg) => {
        if (status === true) {
          this.setState({
            error: {
              target: null,
              msg: "",
            },
          });
        } else {
          this.setState({
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }
      });
    }
  }

  submitLogin = (e: any) => {
    e.preventDefault();
    // stakeholder_id
    // qualification_id
    // school_code
    // sector_code
    // role_id
    // position_id
    // start_date_in_the_school
    // country_id

    // if (this.state.nid === "")
    //   return this.setState({
    //     error: { target: "nid", msg: "Required!" },
    //   });

    if (this.props.basic.academic_calender === null)
      return this.setState({
        error: {
          target: "main-error",
          msg: "You have no academic selected, Please refresh the page.",
        },
      });

    //* Save data
    // FC_AssignCandidate(SendData, (status, msg) => {
    //   if (status === true) {
    //     this.setState({
    //       loading: false,
    //       error: {
    //         target: "main-success",
    //         msg: "Staff created successfully",
    //       },
    //     });
    //     this.props.success(SendData);
    //   } else {
    //     this.setState({
    //       loading: false,
    //       error: {
    //         target: "main-error",
    //         msg: msg,
    //       },
    //     });
    //   }

    //   setTimeout(() => {
    //     this.setState({
    //       error: {
    //         target: null,
    //         msg: "",
    //       },
    //     });
    //   }, 3000);
    // });
  };

  selectSchool = (el: SchoolInterface | null) => {
    if (el !== null && this.props.basic.academic_calender !== null) {
      this.setState({ school_loading: true });
      FC_GetSchoolDetailsRegister(
        el.school_code,
        this.props.basic.academic_calender.academic_year_id,
        (status, data, msg) => {
          if (status === true) {
            this.setState({
              school_loading: false,
              school_details: data,
              error: {
                target: null,
                msg: "",
              },
            });
          } else {
            this.setState({
              school_loading: false,
              school_details: null,
              error: {
                target: "loading_school",
                msg: msg,
              },
            });
          }
        }
      );
    }
    this.setState({ selected_school: el });
  };

  getWaitingList = (data_post: SchoolPositionItem, total_allowed: number) => {
    this.setState({
      loading_waiting_list: true,
      waiting_list: [],
      open_trs_import: true,
      selected_position: data_post,
      selected_total_post_allowed_on_school: total_allowed,
    });
    FC_GetWaitingList((status, data, msg) => {
      if (status === true && data !== null) {
        let found_post = data.find(
          (el) => +el._id === +data_post.position_code
        );

        this.setState({
          loading_waiting_list: false,
          waiting_list: data,
          trs_found_position: found_post === undefined ? null : found_post,
        });
      } else {
        this.setState({
          loading_waiting_list: false,
          waiting_list: [],
          error: {
            target: "waiting-list-error",
            msg: msg,
          },
          trs_found_position: null,
        });
      }
      this.cleanError();
    });
  };

  closeTRSModal = () =>
    this.setState({
      loading_waiting_list: false,
      waiting_list: [],
      open_trs_import: false,
      selected_position: null,
      input_requesting_number: 0,
      selected_post_request_id: "",
    });

  // clean error
  cleanError = () =>
    setTimeout(() => {
      this.setState({
        error: {
          target: null,
          msg: "",
        },
      });
    }, 3000);

  submitDataRequestTRS = (e: any) => {
    e.preventDefault();

    if (this.state.trs_found_position === undefined) {
      return this.setState({
        error: {
          target: "input-number-error",
          msg: "Failed to calculate the number",
        },
      });
    }

    if (this.state.input_requesting_number <= 0) {
      return this.setState({
        error: {
          target: "input-number-error",
          msg: "The number must be greater than one",
        },
      });
    }

    let trs_found_pos =
      this.state.trs_found_position === null
        ? 0
        : this.state.trs_found_position.waiting;
    //* Validation
    if (
      this.state.input_requesting_number >
        +this.state.selected_total_post_allowed_on_school ||
      this.state.trs_found_position === undefined ||
      this.state.input_requesting_number > trs_found_pos
    ) {
      return this.setState({
        error: {
          target: "input-number-error",
          msg: "The number must not be above the vacant post or the available candidates in waiting list",
        },
      });
    }

    if (
      this.props.auth.district === null ||
      // this.state.selected_post_request_id === "" ||
      this.state.selected_school === null ||
      this.state.selected_position === null
    )
      return this.setState({
        error: {
          target: "input-number-error",
          msg: "This post does not fit the requirements, please restart the process or refresh the browser",
        },
      });

    // this.props.auth.district
    let district_code = this.state.selected_school.school_code.substring(0, 2);
    let post_request_id = this.state.selected_post_request_id;
    let school_code = this.state.selected_school.school_code;
    let position_code = this.state.selected_position?.position_code;
    let province_code = this.state.selected_school.school_code.substring(0, 1); //
    let sector_code = this.state.selected_school.school_code.substring(0, 4);
    let academic_id = this.props.basic.academic_calender?.academic_year_id;

    let SendData = {
      available_places: [
        {
          position_code: position_code,
          requested_teacher: this.state.input_requesting_number,
        },
      ],
      disability: false,
      district_code: district_code,
      gender: "",
      in_education: false,
      post_request_id: post_request_id,
      school_code: school_code,
      province_code: province_code,
      sector_code: sector_code,
      academic_year_id: academic_id,
    } as RequestInTRS;
    //
    this.setState({
      loading_candidates_from_trs: true,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_RequestTeachersInTRS(position_code, SendData, (status, data, err) => {
      if (status === true && data !== null) {
        this.setState({
          loading_candidates_from_trs: false,
          error: {
            target: "input-number-error",
            msg: err,
          },
          collected_teachers: data,
          // loading_send_list_to_tmis: true,
          collected_candidates_open: true,
        });

        this.SendTeachersInTMIS(data);
      } else {
        this.setState({
          loading_candidates_from_trs: false,
          error: {
            target: "input-number-error",
            msg: err,
          },
        });
      }
    });
  };

  SendTeachersInTMIS = (data: ReturnedCandidates[]) => {
    this.setState({
      loading_send_list_to_tmis: true,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_SendRequestedTeachersToTRS(data, (status, data, msg) => {
      if (status === true) {
        this.setState({
          loading_send_list_to_tmis: false,
          error: {
            target: "send-list-to-tmis-success",
            msg: "Registration process completed successfully",
          },
        });
      } else {
        this.setState({
          loading_send_list_to_tmis: false,
          error: {
            target: "send-list-to-tmis-error",
            msg: "Failed to complete the registration process.",
          },
        });

        setTimeout(() => {
          if (this.state.collected_teachers.length > 0) {
            this.SendTeachersInTMIS(this.state.collected_teachers);
          }
        }, 2000);
      }
    });
  };

  render() {
    // Render the list
    if (this.state.collected_candidates_open === true)
      return (
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="flex border-b p-2">
            <div className="flex-1 text-center">
              <Heading size="md">New Candidates list form TRS</Heading>
            </div>
          </div>
          <div className="p-2">
            <div className="grid grid-cols-2 gap-3 w-full">
              {[
                {
                  title: "Total vacant post",
                  value: this.state.selected_total_post_allowed_on_school,
                  special: false,
                },

                {
                  title: "Position code",
                  value: this.state.selected_position?.position_code,
                  special: false,
                },
              ].map((itm, i) => (
                <div
                  key={i}
                  className={`bg-gray-50 py-1 px-2 border rounded flex-1 `}
                >
                  <span className="text-gray-500 block">{itm.title}:</span>{" "}
                  <b>{itm.value}</b>
                </div>
              ))}
            </div>

            <div className="grid grid-cols-2 gap-3 w-full mt-2">
              {[
                {
                  title: "School name",
                  value: this.state.selected_school?.school_name,
                  special: false,
                },
                {
                  title: "Position name",
                  value: this.state.selected_position?.position_name,
                  special: false,
                },
              ].map((itm, i) => (
                <div
                  key={i}
                  className={`bg-gray-50 py-1 px-2 border rounded flex-1 `}
                >
                  <span className="text-gray-500 block">{itm.title}:</span>{" "}
                  <b>{itm.value}</b>
                </div>
              ))}
            </div>
          </div>
          <div className="p-2">
            <div className="pb">
              {this.state.loading_send_list_to_tmis === true && (
                <Alert theme="info">
                  <div className="flex gap-2">
                    <Loading />{" "}
                    <p className="pl-4">
                      Completing the process, don't close the window
                    </p>
                  </div>
                </Alert>
              )}

              {this.state.error.target === "send-list-to-tmis-success" && (
                <Alert theme="success">{this.state.error.msg}</Alert>
              )}
              {this.state.error.target === "send-list-to-tmis-error" && (
                <div>
                  <Alert theme="danger">
                    {this.state.error.msg}, please wait
                  </Alert>
                </div>
              )}
            </div>
            <table className="w-full">
              <thead>
                <tr>
                  <th className="p-2 border">#</th>
                  <th className="p-2 border">NID</th>
                  <th className="p-2 border">Names</th>
                  <th className="p-2 border">Phone</th>
                  <th className="p-2 border">Email</th>
                  <th className="p-2 border">Gender</th>
                </tr>
              </thead>
              <tbody>
                {this.state.collected_teachers.length <= 0 ? (
                  <tr>
                    <td colSpan={6} className="py-5 text-center">
                      <Heading>No candidates found!</Heading>
                      <p>NO results found</p>
                    </td>
                  </tr>
                ) : (
                  this.state.collected_teachers.map((cand, i) => (
                    <tr key={i}>
                      <td className="p-2 border">{i + 1}</td>
                      <td className="p-2 border">{cand?.user_info?.n_id}</td>
                      <td className="p-2 border">
                        {cand?.user_info?.user_info?.fname}{" "}
                        {cand?.user_info?.user_info?.lname}
                      </td>
                      <td className="p-2 border">
                        {cand?.user_info?.phone_number}
                      </td>
                      <td className="p-2 border">{cand?.user_info?.email}</td>
                      <td className="p-2 border">
                        {cand?.user_info?.user_info?.sex}
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
            <div className="p-2 text-right">
              {this.state.loading_send_list_to_tmis === false && (
                <Link to="/redirect-register">
                  <Button type="button">
                    Add candidates to others position
                  </Button>
                </Link>
              )}
            </div>
          </div>
        </DashboardContainer>
      );

    // Import teachers from trs
    if (this.state.open_trs_import === true)
      return (
        <DashboardContainer>
          <div className="flex border-b p-2">
            <div className="flex-1">
              <Heading>Register Teachers form TRS</Heading>
            </div>
            <div className="">
              <Button theme="danger-light" onClick={this.closeTRSModal}>
                Close
              </Button>
            </div>
          </div>
          <div className="p-2">
            {this.state.error.target === "waiting-list-error" && (
              <Alert theme="danger" className="my-3">
                {this.state.error.msg}
              </Alert>
            )}
            {this.state.loading_waiting_list === true ? (
              <div className="text-6xl text-center animate__animated animate__zoomIn py-3">
                <Heading size="sm">
                  Checking in <b>TRS</b> waiting list
                </Heading>
                <Loading className="mx-auto block text-center" />
              </div>
            ) : this.state.trs_found_position === null ? (
              <div className="py-8 text-center">
                <Heading size="md" className="text-center">
                  No candidate found on <br />
                  <b className="text-blue-400">
                    {this.state.selected_position?.position_name}
                  </b>
                </Heading>
                <Heading size="sm">From Teacher Recruitment System</Heading>
              </div>
            ) : (
              <div className="w-full">
                <div className="grid grid-cols-3 gap-3 w-full">
                  {[
                    {
                      title: " Total teachers in waiting list",
                      value: this.state.trs_found_position.waiting,
                      special: true,
                    },
                    {
                      title: "Total vacant post",
                      value: this.state.selected_total_post_allowed_on_school,
                      special: false,
                    },

                    {
                      title: "Position code",
                      value: this.state.selected_position?.position_code,
                      special: false,
                    },
                  ].map((itm, i) => (
                    <div
                      key={i}
                      className={`bg-gray-50 py-1 px-2 border rounded flex-1 `}
                    >
                      <span className="text-gray-500 block">{itm.title}:</span>{" "}
                      <b>{itm.value}</b>
                    </div>
                  ))}
                </div>

                <div className="grid grid-cols-2 gap-3 w-full mt-2">
                  {[
                    {
                      title: "School name",
                      value: this.state.selected_school?.school_name,
                      special: false,
                    },
                    {
                      title: "Position name",
                      value: this.state.selected_position?.position_name,
                      special: false,
                    },
                  ].map((itm, i) => (
                    <div
                      key={i}
                      className={`bg-gray-50 py-1 px-2 border rounded flex-1 `}
                    >
                      <span className="text-gray-500 block">{itm.title}:</span>{" "}
                      <b>{itm.value}</b>
                    </div>
                  ))}
                </div>

                <div className="p-1 pt-5">
                  <form
                    className="max-w-sm"
                    onSubmit={this.submitDataRequestTRS}
                  >
                    {this.state.error.target ===
                      "get-teachers-from-trs-success" && (
                      <Alert theme="success">{this.state.error.msg}</Alert>
                    )}
                    <TextInput
                      error={
                        this.state.error.target === "input-number-error"
                          ? this.state.error.msg
                          : ""
                      }
                      label="Type the number of teachers you need"
                      name=""
                      type="number"
                      onChange={(e) =>
                        this.setState({
                          input_requesting_number: +e.target.value,
                          error: {
                            target: null,
                            msg: "",
                          },
                        })
                      }
                      disabled={this.state.loading_candidates_from_trs}
                      value={this.state.input_requesting_number}
                      placeholder="Number of posts you want"
                    />
                    <Button loading={this.state.loading_candidates_from_trs}>
                      Request and Register teacher{"("}s{")"} from TRS
                    </Button>
                  </form>
                </div>
              </div>
            )}
          </div>
        </DashboardContainer>
      );

    // laoding
    if (
      this.props.basic.assign_teacher_assets.loading === true ||
      this.props.basic.assign_teacher_assets.data === null
    )
      return (
        <DashboardContainer>
          <div className="py-6 mx-auto text-center">
            <Loading />
          </div>
        </DashboardContainer>
      );

    let SchoolsList: SchoolInterface[] = search(
      this.props.basic.assign_teacher_assets.data.schools,
      this.state.search_school,
      {
        school_code: true,
        school_name: true,
        school_category: true,
        school_id: true,
        school_status: true,
        region_code: true,
      }
    );
    if (this.props.district_id === undefined)
      return <Alert theme="danger">No district found!</Alert>;

    return (
      <>
        {this.state.error.target === "main-error" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          {this.state.selected_school !== null ? (
            <div className="p-2">
              <div className="flex">
                <div className=" mr-2">
                  <Button
                    size="sm"
                    theme="primary"
                    onClick={this.selectSchool.bind(this, null)}
                  >
                    Back
                  </Button>
                </div>
                <div className="flex-1">
                  <div className="">
                    <h2 className="text-2xl  mb-2">School details</h2>
                    <p>
                      School code:{" "}
                      <b>{this.state.selected_school.school_code}</b>
                    </p>
                    <p>
                      School name:{" "}
                      <b>{this.state.selected_school.school_name}</b>
                    </p>
                    <p>
                      School status:{" "}
                      <b>{this.state.selected_school.school_status}</b>
                    </p>
                    <p>
                      School category:{" "}
                      <b>{this.state.selected_school.school_category}</b>
                    </p>
                  </div>
                </div>
              </div>

              <hr className="my-2" />

              {this.state.school_loading === true ? (
                <Loading />
              ) : (
                <div className="animate__animated animate__zoomIn animate__faster p-2">
                  <Heading size="md">Available positions</Heading>
                  <table className="w-full">
                    <thead>
                      <tr className="text-left">
                        <th className="p-1 border text-left">#</th>
                        <th className="p-1 border text-left">Position code</th>
                        <th className="p-1 border text-left">Position name</th>
                        <th className="p-1 border text-left">Qualification</th>
                        <th className="p-1 border text-left">Existing posts</th>
                        <th className="p-1 border text-left">Occupied posts</th>
                        <th className="p-1 border text-left">Vacant post</th>
                        <th className="p-1 border text-left w-44"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {!this.state.school_details ? (
                        <tr>
                          <td colSpan={6} className="p-3 text-center">
                            <Heading size="md">No post found</Heading>
                            <p>No post found</p>
                          </td>
                        </tr>
                      ) : this.state.school_details.school_positions.length <=
                        0 ? (
                        <tr>
                          <td
                            colSpan={7}
                            className="text-center py-6 opacity-60"
                          >
                            <Heading>No post found!</Heading>
                          </td>
                        </tr>
                      ) : (
                        this.state.school_details.school_positions.map(
                          (pos, i) => {
                            // let occupied_school_position =
                            //   this.state.school_details?.occupied_school_positions.find(
                            //     (el) => el.position_code === pos.position_code
                            //   );

                            // Data is here
                            let school_allowed_post =
                              this.state.school_details?.school_allowed_posts.find(
                                (el) => el.position_code === pos.position_code
                              );

                            // let total_allowed_number =
                            //   school_allowed_post === undefined
                            //     ? 0
                            //     : +school_allowed_post.allowed_number;
                            // let occupied_position =
                            //   occupied_school_position === undefined
                            //     ? 0
                            //     : +occupied_school_position.occupied_number;

                            // let total =
                            //   total_allowed_number - occupied_position;

                            // let total_exist_on_post =
                            //   occupied_school_position === undefined
                            //     ? 0
                            //     : occupied_school_position.occupied_number;
                            let total_exist_on_post =
                              school_allowed_post === undefined
                                ? 0
                                : school_allowed_post.posts;

                            let total_vacant_posts =
                              school_allowed_post === undefined
                                ? 0
                                : +school_allowed_post.vacant_posts;

                            let total_allowed =
                              school_allowed_post === undefined
                                ? 0
                                : school_allowed_post.occupied_number;

                            return (
                              <tr
                                key={i}
                                className="hover:bg-blue-600 hover:bg-opacity-20"
                              >
                                <td className="p-2 text-center border">
                                  {i + 1}
                                </td>
                                <td className="p-2 border">
                                  {pos.position_code}
                                </td>
                                <td className="p-2 border">
                                  {pos.position_name}
                                </td>
                                <td className="p-2 border">
                                  {pos.qualification_name}
                                </td>
                                <td className="p-2 border">
                                  {total_exist_on_post}
                                </td>
                                <td className="p-2 border">{total_allowed}</td>
                                <td className="p-2 border">
                                  {total_vacant_posts > 0
                                    ? total_vacant_posts
                                    : 0}
                                </td>
                                <td className="p-2 border">
                                  {/* {total_vacant_posts >= 1 && ( */}
                                  <>
                                    <Button
                                      className="w-full"
                                      size="sm"
                                      theme="accent-light"
                                      onClick={() => {
                                        if (
                                          this.state.selected_school !== null
                                        ) {
                                          this.props.success({
                                            position_id: pos.position_code,
                                            position_name: pos.position_name,
                                            qualification_id:
                                              pos.qualification_id,
                                            qualification_name:
                                              pos.qualification_name,
                                            school_code:
                                              this.state.selected_school
                                                .school_code,
                                            school_name:
                                              this.state.selected_school
                                                .school_name,
                                          });
                                        }
                                      }}
                                    >
                                      Register new staff
                                    </Button>
                                    {/* <Button
                                        theme="success-light"
                                        size="sm"
                                        className="w-full mt-1"
                                        onClick={this.getWaitingList.bind(
                                          this,
                                          pos,
                                          +total_vacant_posts
                                        )}
                                      >
                                        Import from TRS
                                      </Button> */}
                                  </>
                                  {/* )} */}
                                </td>
                              </tr>
                            );
                          }
                        )
                      )}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          ) : (
            <div className="py-2">
              <h2 className="text-2xl">Select a school</h2>
              <div className="p-1">
                <TextInput
                  onChange={(e) =>
                    this.setState({ search_school: e.target.value })
                  }
                  value={this.state.search_school}
                  error={""}
                  label={"Search school"}
                  placeholder={"Search by school name,..."}
                  name={""}
                />
              </div>

              <table className="w-full">
                <thead>
                  <tr>
                    <th className="p-1 border">#</th>
                    <th className="p-1 border">school name</th>
                    <th className="p-1 border">School</th>
                    <th className="p-1 border">Category</th>
                    <th className="p-1 border">Total</th>
                  </tr>
                </thead>
                <tbody>
                  {SchoolsList.length <= 0 ? (
                    <tr>
                      <td>
                        <h2>Creative data are here</h2>
                      </td>
                    </tr>
                  ) : (
                    SchoolsList.map((itm, i) => {
                      return (
                        <tr
                          key={i}
                          className="hover:bg-blue-500 hover:bg-opacity-20"
                        >
                          <td className="border p-1">{i + 1}</td>
                          <td className="border p-1">{itm.school_code}</td>
                          <td className="border p-1">{itm.school_name}</td>
                          <td className="border p-1">{itm.school_category}</td>
                          <td className="border p-1 w-32">
                            <Button
                              size="sm"
                              theme="accent-light"
                              className="w-full"
                              onClick={this.selectSchool.bind(this, itm)}
                            >
                              Select school
                            </Button>
                          </td>
                        </tr>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
          )}
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const RegisterTeacherDDE = connect(mapStateToProps, {
  FC_GetAssignAssets,
})(_RegisterTeacherDDE);
